@import url(https://fonts.googleapis.com/css?family=Pacifico);
@import url(https://fonts.googleapis.com/css?family=Abril+Fatface);

body {
  margin: 0;
  padding: 0;
  font-family: verdana, Lucida, Lucida;
 
}

.searchContainer {
    background: rgba(244,103,50,1)
}

.searchBox {
    text-align: center;
    padding: 30px 0 0 0;
}

.search {
    text-align: center;
    padding: 15px;
    border-radius: 9px;
    background-color: rgba(0, 0, 0, 0.705);
    border: 1px solid rgba(255, 255, 255, 0.829);
    font-size: large;
    text-transform: capitalize;
    color: #fff;
}
.card {
    height: 25vh;
    width: 100vw;
    background-color: rgb(221, 98, 0);
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-hyphens: auto;
        -ms-hyphens: auto;
            hyphens: auto;
    transition: all 1s;
    font-size: 1.2em;
}

.card:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    opacity: 0.8;
    transition: all 1s;
}

@media screen and (min-width: 480px) {
    .card {
        height: 26vh;
        width: 40vw;
        border-radius: 10px;
        font-size: 1.5em;
    }
}

@media screen and (min-width: 720px) {
    .card {
        height: 25vw;
        width: 25vw;
    }
}

@media screen and (min-width: 720px) {
    .card {
        height: 25vw;
        width: 25vw;
    }
}

@media screen and (min-width: 920px) {
    .card {
        height: 15vw;
        width: 15vw;
    }
}
h2 {
    font-size: 2em;
}

.region {
    /* width: 300px; */
    width: 100%;
}

.region-card {
    width: 400px;
    height: 400px;
    list-style: none;
    /* vertical-align: top; */
}

.region-card h3 {
    cursor: pointer;
}

.regiontext{
    font-family: 'Pacifico', cursive;
    font-size: 1rem;

}
.list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    padding: 0;
    margin: 1.1em;
}

.header{
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), 
                                    url('https://images.unsplash.com/photo-1530521954074-e64f6810b32d?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=375f08acd511ccc0cc6ca5db9979c7b1&auto=format&fit=crop&w=1350&q=80');
    background-attachment: fixed;
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: cover;
}

.headertext{
    background: #fff; 
    letter-spacing: 7px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
    font-family: 'Abril Fatface', cursive;
    font-size: 6em;
    text-transform: uppercase;
}

.nav {
    background-color: rgba(0, 0, 0, 0.705);
    position: relative;
    bottom: -35px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.centerFlex {
  padding: 20px 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#f46732+0,fcb46f+100&1+12,0.4+100 */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(244,103,50,1) 0%,rgba(245,112,57,1) 12%,rgba(252,180,111,0.4) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f46732', endColorstr='#66fcb46f',GradientType=0 ); /* IE6-9 */
}

.region-card {
  padding-top: 15px;
}

.region {
  padding-top: 15px;
}
